/**
 * base
 */
html {
  height: 100%;
  width: 100%;
}

body {
  color: #333;
  font-family: "Helvetica Neue", Arial, "Hiragino Sans", "Hiragino Kaku Gothic ProN", "BIZ UDPGothic", Meiryo, sans-serif;
  font-feature-settings: "palt";
  line-height: 1.5;
  width: 1920px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  overflow-x: hidden;
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
}

/**
 * l-wrapper
 */
.l-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.l-wrapper > main {
  flex: 1;
  overflow-x: hidden;
}

/**
 * l-header
 */
.l-header {
  padding: 20px 0;
}
.l-header__inner {
  display: grid;
  place-items: center;
  width: min(100% - 80px, 1280px);
  margin-inline: auto;
}
.l-header__title {
  font-size: 2.5rem;
  text-align: center;
}
.l-header__nav {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header__nav-item + .l-header__nav-item {
  margin-left: 40px;
}

/**
 * l-hero
 */
.l-hero {
  min-height: calc(var(--vh, 1vh) * 100);
}

/**
 * l-main
 */
.l-main {
  padding-top: 40px;
  padding-bottom: 40px;
}

/**
 * l-footer
 */
.l-footer {
  padding-top: 20px;
  padding-bottom: 20px;
}
.l-footer__text {
  text-align: center;
}

/**
 * c-title
 */
.c-title {
  font-family: "Roboto", "Montserrat";
  padding-left: 100px;
  position: relative;
}
.c-title::before {
  content: attr(data-number);
  color: currentColor;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.c-title::after {
  background-color: #0000b3;
  content: "";
  width: fib(10);
  height: 1px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 33px;
  transform: translateY(-50%);
}

/**
 * c-btn
 */
/**
 * c-card
 */
/**
 * c-ex-link
 */
/**
 * p-index-fv
 */
.p-index-fv__visual source,
.p-index-fv__visual img {
  display: block;
  width: 640px;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
}

/**
 * u-text-link
 */
/**
 * u-uppercase
 */
.u-uppercase {
  text-transform: uppercase;
}

/**
 * u-visibility-hidden
 */
.u-visibility-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}

